import {useEffect, useState} from 'react';
import {useDispatch, useSelector} from "react-redux";
import styled from "styled-components";

import Page from "../../components/Page";
import {DEFAULT_MARGIN_MARKET} from "../../constants";
import {fetchMarket} from "../../state/margin/actions";
import {useActiveWeb3React} from "../../hooks";
import StyledCard, {ResponsiveCard} from "../../components/Card";
import Orderbook from "./components/Orderbook";
import TradeHistory from "./components/TradeHistory";
import MarketDetails from "./components/MarketDetails";
import TradingViewWidget, {Themes} from "react-tradingview-widget";
import OrderHistory from "./components/OrderHistory";

const Row = styled.div`
  display: ${({ justMobile }) => justMobile ? 'none' : "flex"};
  margin-left: -5px;
  margin-right: -5px;
  
  @media (max-width: 1400px) {
    flex-direction: column;
    display: ${({ justDesktop }) => justDesktop ? 'none' : "flex"};
  }
`
const Col = styled.div`
  padding: 0 5px;
  display: flex;
  flex-direction: column;
`


const ContainerCard = styled(ResponsiveCard)`
  display: flex;
  margin-bottom: 20px;
  
  & .card-body {
    padding: 30px;
  }
  
  @media (min-width: 1400px) {
    width: 582px;
    height: 882px;
    margin-bottom: 10px;
  
    & .card-body {
      padding: 12px 20px 20px;
      display: flex;
    }
`

const Card = styled(StyledCard)`
  display: flex;
  flex-direction: column;
  align-items: stretch;
  margin-bottom: 20px;
  overflow: hidden;
  
    & .card-body {
      padding: 0;
    }
    @media (min-width: 1400px) {
      margin-bottom: 10px;
      width: 570px;
      height: 373px;
    }
`

const Margin = props => {
    const { account } = useActiveWeb3React();
    const dispatch = useDispatch()
    const params = props.match.params;

    useEffect(() => {
        if(!params.market) {
            props.history.replace(`${props.match.url}/${DEFAULT_MARGIN_MARKET}`)
        } else {
            dispatch(fetchMarket(account, params.market))
        }
    }, [params.market, account])

    return (
        <Page title={false} morePadding size={'xl'}>
            <Row>

                <Col>
                    <MarketDetails isTradingGraphic={true}/>
                    <Card>
                        <TradingViewWidget
                            symbol={(params.market || DEFAULT_MARGIN_MARKET).split('-').join("")}
                            theme={Themes.DARK}
                            allow_symbol_change={false}
                            width={'100%'}
                            height={373}
                        />
                    </Card>
                    <OrderHistory/>
                </Col>
                <Col>
                    <ContainerCard>
                        <Orderbook/>
                        <TradeHistory/>
                    </ContainerCard>
                </Col>
            </Row>
        </Page>
    )

}

export default Margin;
